export const version = '0.0.1'; // 版本号

// 是否是函数
export function isFunction(obj) {
  return Object.prototype.toString.call(obj) === '[object Function]';
}

// 是否是字符串
export function isString(obj) {
  return Object.prototype.toString.call(obj) === '[object String]';
}

// 是否是object对象
export function isObject(obj) {
  return Object.prototype.toString.call(obj) === '[object Object]';
}

// 是否是数组
export function isArray(obj) {
  return Object.prototype.toString.call(obj) === '[object Array]';
}

// 是否是window对象
export function isWindow(obj) {
  return obj != null && obj === obj.window;
}

// 是否是纯对象
export function isPlainObject(obj) {
  return (
    isObject(obj) &&
    !isWindow(obj) &&
    Object.getPrototypeOf(obj) === Object.prototype
  );
}

// 是否是appcan环境
export function isAppCan() {
  return window.navigator.userAgent.toLowerCase().indexOf('appcan') >= 0;
}

/*
  检查是ready
  @param Function callback 回调函数

  */
export function ready(callback) {
  let cb = callback;
  cb = isFunction(callback) ? callback : function func() {};
  if (isAppCan()) {
    window.uexOnload = function onLoad(type) {
      cb(type);
    };
  } else {
    window.onload = function onload() {
      cb();
    };
  }
}

// 获取随机的唯一id，随机不重复，长度固定
export function getUID(len) {
  let length = len;
  length = length || 6;
  length = parseInt(length, 10);
  length = isNaN(length) ? 6 : length;
  const seed = '0123456789abcdefghijklmnopqrstubwxyzABCEDFGHIJKLMNOPQRSTUVWXYZ';
  const seedLen = seed.length - 1;
  let uuid = '';
  let count = length;
  while (count) {
    count -= 1;
    uuid += seed[Math.round(Math.random() * seedLen)];
  }
  return uuid;
}



