/* window模块 base onKeyPressed，closeWindow */
import { isPlainObject, isFunction } from './base';
import { val } from './storage';

// android物理键监听拦截
export function onKeyPressed(code, callback) {
  let keyCallBack = callback;
  let keyCode = code;
  if (arguments.length === 1 && isPlainObject(code)) {
    keyCallBack = code.success;
    keyCode = code.code;
  }
  if (isFunction(keyCallBack)) {
    uexWindow.onKeyPressed = (keyCode1) => {
      keyCallBack(keyCode1);
    };
  }
  uexWindow.setReportKey(keyCode, 1);
}

/**
 * 关闭当前窗口
 *
 */
export function closeWindow() {
  uexWindow.close(-1);
}

/**
 * 修改当前页面标题(健康号专用)
 * @param String title
 */
export function setWindowTitle(title) {
  document.title = title;
  const windowOption = {
    windowTitle: title,
  };
  uexWindow.setWindowOptions({ windowOptions: windowOption });
}

/**
 * 设置健康号右侧menu选项(健康号专用)
 * @param Array menus ["pyq","weixin","qq","weibo","refresh","copyUrl","openInBrowser"]
 */
export function setTitleMenu(menus) {
  const curhref = window.location.href;
  val(encodeURIComponent(curhref), menus);
}
/**
 * 获取健康号右侧menu选项，未设置时默认返回["refresh","copyUrl","openInBrowser"]
 */
export function getTitleMenu() {
  const curhref = window.location.href;
  const menus = val(encodeURIComponent(curhref)) || '["refresh","copyUrl","openInBrowser"]';
  return JSON.parse(menus);
}

/**
 * 修改当前页面标题颜色(健康号专用)
 * @param String color
 */
export function setTitleBackgroundColor(color) {
  const windowOption = {
    titleBarBgColor: color,
  };
  uexWindow.setWindowOptions({ windowOptions: windowOption });
}



