import axios from 'axios';
import * as base from './base';
import * as device from './device';
import * as image from './image';
import * as location from './location';
import logs from './log';
import * as scanner from './scanner';
import * as voice from './voice';
import * as window from './window';
import myAdapter from './xhr';
import * as download from './download';
import upload from './upload';
import * as storage from './storage';

if (base.isAppCan()) {
  axios.defaults.adapter = myAdapter;
}

// console.log(axios.defaults.adapter);

const appcan = {
  request: axios,
  download: download.download,
  cancelDownload: download.cancelDownload,
  getDownloadInfo: download.getDownloadInfo,
  upload,
  ready: base.ready,
  getUID: base.getUID,
  isAppCan: base.isAppCan,
  version: base.version,
  getInfo: device.getInfo,
  getNetworkType: device.getNetworkType,
  takePicture: image.takePicture,
  takePicturePromise: image.takePicturePromise,
  chooseImage: image.chooseImage,
  chooseImagePromise: image.chooseImagePromise,
  previewImage: image.previewImage,
  previewImagePromise: image.previewImagePromise,
  openCropper: image.openCropper,
  openCropperPromise: image.openCropperPromise,
  getLocation: location.getLocation,
  getLocationPromise: location.getLocationPromise,
  logs,
  scanQRCode: scanner.scanQRCode,
  scanImage: scanner.scanImage,
  scanImagePromise: scanner.scanImagePromise,
  startRecord: voice.startRecord,
  stopRecord: voice.stopRecord,
  stopRecordPromise: voice.stopRecordPromise,
  playVoice: voice.playVoice,
  pauseVoice: voice.pauseVoice,
  stopVoice: voice.stopVoice,
  onVoicePlayEnd: voice.onVoicePlayEnd,
  onVoicePlayEndPromise: voice.onVoicePlayEndPromise,
  onKeyPressed: window.onKeyPressed,
  closeWindow: window.closeWindow,
  setWindowTitle: window.setWindowTitle,
  setTitleBackgroundColor: window.setTitleBackgroundColor,
  setTitleMenu: window.setTitleMenu,
  getTitleMenu: window.getTitleMenu,
  setVal: storage.setVal,
  getVal: storage.getVal,
  getKeys: storage.getKeys,
  clear: storage.clear,
  leaveSpace: storage.leaveSpace,
  val: storage.val,
};
// Object.assign(appcan, base, device, image, location, logs, scanner, voice, window);
export default appcan;



