import { isPlainObject } from './base';

/**
 * 获取经纬度
 * @param  String   type     指定坐标系类型,
 * "wgs84":采用世界标准经纬度坐标;
 * "bd09":采用百度地图的经纬度坐标;
 * "gcj02":采用高德地图的经纬度坐标.
 * 不传,iOS默认返回高德地图的经纬度坐标,
 * Android默认返回百度地图的经纬度坐标
 * @param  Function callback 回调
 * @return  {error:null,latitude:lat, longitude:log}
 */
export function getLocation(type, callback) {
  let locationCallBack = callback;
  let locationType = type;
  if (arguments.length === 1 && isPlainObject(type)) {
    locationCallBack = type.success;
    locationType = type.type;
  }
  uexLocation.onChange = (lat, log) => {
    uexLocation.closeLocation();
    const res = { error: null, latitude: lat, longitude: log };
    locationCallBack(res);
  };

  uexLocation.openLocation(locationType, (error) => {
    if (error) {
      const res = { error: new Error('openLocation error') };
      locationCallBack(res);
    }
  });
}

export function getLocationPromise(type) {
  return new Promise(((resolve, reject) => {
    uexLocation.onChange = (lat, log) => {
      uexLocation.closeLocation();
      return resolve({ latitude: lat, longitude: log });
    };
    uexLocation.openLocation(type, (error) => {
      if (error) {
        return reject(new Error('openLocation error'));
      }
      return '';
    });
  }));
}



