/*
  config = {
    headers : {"Content-type":"application/json;charset=utf-8"},
    url: 'http://nvie.com/files/Git-branching-model.pdf',
    path: 'wgt://data/down/Git-branching-model.pdf',
    mode: '0',
    onDownloadProgress: function (progressEvent) {
      // Do whatever you want with the native progress event
    }
  }
*/

const defaultsConfig = {
  path: 'wgt://data/down/',
  mode: 1,
};

export function download(url, config = defaultsConfig) {
  return new Promise((resolve, reject) => {
    const downloader = uexDownloaderMgr.create();

    let onProgress = null;
    // Handle progress if needed
    if (typeof config.onDownloadProgress === 'function') {
      onProgress = config.onDownloadProgress;
    }

    if (downloader) {
      if (config.headers) uexDownloaderMgr.setHeaders(downloader, config.headers);

      uexDownloaderMgr.download(downloader, url, config.path, config.mode,
        (fileSize, percent, status) => {
          switch (status) {
          case 0:
            if (onProgress) onProgress({ percent });
            break;
          case 1:
            resolve({ percent });
            uexDownloaderMgr.closeDownloader(downloader);
            break;
          case 2:
            reject('download failed');
            uexDownloaderMgr.closeDownloader(downloader);
            break;
          default:
            break;
          }
        });
    } else {
      // create failed
      reject('create failed');
    }
  });
}

export function cancelDownload(url, mode = 0) {
  uexDownloaderMgr.cancelDownload(url, mode);
}

export function getDownloadInfo(url) {
  return new Promise((resolve, reject) => {
    const info = uexDownloaderMgr.getInfo(url);

    if (info) {
      resolve(info);
    } else {
      reject('getDownloadInfo faild');
    }
  });
}



