import { isPlainObject } from './base';

/**
 * 后台开始录音
 * @param  Number mode     必填 要录音格式,0:AMR格式,1:CAF格式,2:MP3格式.Android只支持AMR格式,MP3格式.
 * @param  String fileName 否   录音文件名称,不传时默认以当前时间作为文件名
 * @return {[type]}          [description]
 */
export function startRecord(mode, fileName) {
  let recordMode = mode;
  let recordFileName = fileName;
  if (arguments.length === 1 && isPlainObject(mode)) {
    recordMode = mode.mode;
    recordFileName = mode.fileName;
  }
  uexAudio.startBackgroundRecord(recordMode, recordFileName);
}

/**
 * 停止录音
 * @param  Function callback 录音结束后,会调用此回调函数
 * 回调参数为录音文件路径
 * @return
 */
export function stopRecord(callback) {
  uexAudio.stopBackgroundRecord((filePath) => {
    callback(filePath);
  });
}

export function stopRecordPromise() {
  return new Promise(((resolve) => {
    uexAudio.stopBackgroundRecord(filePath => resolve(filePath));
  }));
}

/**
 * 播放语音
 * @param  String path 语音路径
 * @return null
 */
export function playVoice(path) {
  let param = {
    playMode: 0,
  };
  param = JSON.stringify(param);
  uexAudio.setPlayMode(param);
  uexAudio.open(path);
  uexAudio.setProximityState('1');
  uexAudio.play('0');
}

/**
 * 暂停播放语音
 *
 */
export function pauseVoice() {
  uexAudio.pause();
}

/**
 * 停止播放语音
 * @return null
 */
export function stopVoice() {
  uexAudio.stop();
}

/**
 * 监听语音播放完毕接口
 * @param  Function callback 播放完毕回调
 *
 */
export function onVoicePlayEnd(callback) {
  uexAudio.onPlayFinished = () => {
    uexAudio.setProximityState('0');
    callback();
  };
}
export function onVoicePlayEndPromise() {
  return new Promise((resolve) => {
    uexAudio.onPlayFinished = () => {
      uexAudio.setProximityState('0');
      return resolve();
    };
  });
}



