import { isString } from './base';

const storage = window.localStorage;
/*
   从本地存储获取值
   @param String key 设置localstorage的key
   @param String value 设置localstorage的val

   */
export function setVal(key, value) {
  if (storage) {
    let value1 = value;
    if (!isString(value1)) {
      value1 = JSON.stringify(value1);
    }
    storage.setItem(key, value1);
  }
}

/*
   从localStorage获取对应的值
   @param String key 获取值的key

   */
export function getVal(key) {
  if (storage && key) {
    return storage.getItem(key);
  }
  return '';
}

/*
   从localStorage获取所有的keys

   */
export function getKeys() {
  const res = [];
  for (let i = 0, len = storage.length; i < len; i) {
    let key = '';
    key = storage.key(i);
    if (key) {
      res.push(key);
    }
    const n = i + 1;
    i = n;
  }
  return res;
}

/*
   青春对应的key
   @param String key


   */
export function clear(key) {
  if (key && isString(key)) {
    storage.removeItem(key);
  } else {
    storage.clear();
  }
}

/*
   localStorage剩余空间大小

   */
export function leaveSpace() {
  const all = 1024 * 1024 * 5;
  const space =
    all - unescape(encodeURIComponent(JSON.stringify(storage))).length;
  return space;
}

/*
   获取或者设置localStorage的值
   @param String key
   @param String val

   */
export function val(key, value) {
  if (arguments.length === 1) {
    return getVal(key);
  }
  return setVal(key, value);
}



