import { isPlainObject } from './base';
/**
 * 二维码扫一扫
 * @param  Function callback 扫描结果返回回调
 * var callbackFunction = function(error,data){}
 * 参数名称 类型  说明
  error Number  扫描结果,0-成功,非0-失败,打开失败通常是因为没有开启摄像头权限.
  data  Object  扫描成功的数据,形式如下:
  var data = {
      code:,
      type:
  }
  */
export function scanQRCode(callback) {
  uexScanner.open(callback);
}
/**
 * 扫描二维码及条形码图片
 * @param  String imagePath 二维码及条形码图片路径
 * @param  Function callback 二维码识别结果回调
 */
export function scanImage(imagePath, callback) {
  let scannerCallBack = callback;
  let scannerImagePath = imagePath;
  if (arguments.length === 1 && isPlainObject(imagePath)) {
    scannerCallBack = imagePath.success;
    scannerImagePath = imagePath.imagePath;
  }
  uexScanner.recognizeFromImage(scannerImagePath, scannerCallBack);
}

export function scanImagePromise(imagePath) {
  const scannerImagePath = imagePath;
  return new Promise(((resolve) => {
    uexScanner.recognizeFromImage(scannerImagePath, res => resolve(res));
  }));
}



